.project-poast-card-list {
    margin: 20px 0px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    gap: 50px;
}

.postcard {
    width: 400px;
    height: auto;
    background-color: var(--color-light-black);
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0 0 24px 0 var(--color-black);
    transition: .5s ease;
}

.postcard-image-title {
    position: absolute;
    top: 15px;
    left: 15px;
    z-index: 1;
}

.postcard:hover img {
    transform: scale(1.1);
}

.postcard-image-container {
    height: 300px;
    position: relative;
    overflow: hidden;
}

.postcard-image-container img {
    width: 400px;
    height: 300px;
    object-fit: cover;
    transition: .5s ease;
}

.postcard-image-tag-list {
    position: absolute;
    bottom: 15px;
    left: 15px;
    z-index: 1;
    display: flex;
    flex-direction: row;
    gap: 10px;
}

.postcard-image-tag {
    width: fit-content;
    height: auto;
    border-radius: 20px;
    padding: 3px 10px;
    background-color: rgb(255, 255, 255, 0.25);
}

.postcard-image-tag p {
    font-size: 15px;
}

.postcard-description {
    padding: 15px 15px;
}
